@import "src/theme";

.header {
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    background: $transparent-color;

    padding-top: 11vmax;
    // padding-bottom: 0 !important;

    h1 {
        font-family: $header-font-family;
        color: $font-color;
        font-weight: bold;
        font-size: 5vmax;
    }

    h2 {
        font-family: $header-font-family;
        color: $font-color;
        font-weight: bold;
        font-size: 2vmax;
    }
}
