@import "src/theme";


.img {
    font: optional;
    width: 75%;
    height: auto;
}

.details {
    background: $transparent-color;

    .announcement {
        font-family: $header-font-family;
        font-size: 2em;
    }

    .details {
        font-family: $body-font-family;
        font-size: 1.5em;
    }

    .location {
        font-family: $body-font-family;
        font-weight: 100;
        a:link,
        a:visited,
        a:hover,
        a:link {
            color: $font-color;
        }
    }

    .hashtag {
        font-family: $primary-font-family;
        font-weight: 300;
        font-size: 1.1em;
    }
}

.navigation {
    font-family: $body-font-family;
    font-size: 1.3em;
    justify-content: center;

    .nav_link {
        font-size: 1.3em;
        color: $font-color;
        padding: 10px;
    }

    @media (max-width:$width-small) {
        flex-direction: column;
    }

    @media (min-width:$width-small) {
        flex-direction: row;
    }

    .nav_link:hover {
        text-decoration: none;
        text-decoration: underline;
    }

    .active {
        text-decoration: underline;
    }
}
